.card {
  --turn-duration: .5s;
  border-radius: $border-radius-basic;
  overflow: hidden;

  &__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 300px;
    height: 160px;

    overflow: hidden;

    font-size: 32px;
    color: #fff;

    transform: rotateX(180deg);

    &__content {
      width: 100%;
      max-height: 100%;
      overflow: hidden;
      line-height: 1.1;
    }
  }
}

.card--native {
  animation: turn-foreign-to-native var(--turn-duration) ease-in-out forwards;
}

.card--foreign {
  animation: turn-native-to-foreign var(--turn-duration) ease-in-out forwards;
}

.card__content--native {
  animation: turn-content-native var(--turn-duration) ease-in-out forwards;
}

.card__content--foreign {
  animation: turn-content-foreign var(--turn-duration) ease-in-out forwards;
}

.card__content--hide {
  display: none;
}